import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
	import news from '@/views/modules/news/list'
	import fuwujilu from '@/views/modules/fuwujilu/list'
	import aboutus from '@/views/modules/aboutus/list'
	import huodongbaoming from '@/views/modules/huodongbaoming/list'
	import jifenlipin from '@/views/modules/jifenlipin/list'
	import discusszhiyuanhuodong from '@/views/modules/discusszhiyuanhuodong/list'
	import jifenduihuan from '@/views/modules/jifenduihuan/list'
	import storeup from '@/views/modules/storeup/list'
	import config from '@/views/modules/config/list'
	import zhiyuanhuodong from '@/views/modules/zhiyuanhuodong/list'
	import zhiyuanzhe from '@/views/modules/zhiyuanzhe/list'
	import newstype from '@/views/modules/newstype/list'


//2.配置路由   注意：名字
export const routes = [{
	path: '/',
	name: '系统首页',
	component: Index,
	children: [{
		// 这里不设置值，是把main作为默认页面
		path: '/',
		name: '系统首页',
		component: Home,
		meta: {icon:'', title:'center', affix: true}
	}, {
		path: '/updatePassword',
		name: '修改密码',
		component: UpdatePassword,
		meta: {icon:'', title:'updatePassword'}
	}, {
		path: '/pay',
		name: '支付',
		component: pay,
		meta: {icon:'', title:'pay'}
	}, {
		path: '/center',
		name: '个人信息',
		component: center,
		meta: {icon:'', title:'center'}
	}
	,{
		path: '/news',
		name: '公告栏',
		component: news
	}
	,{
		path: '/fuwujilu',
		name: '服务记录',
		component: fuwujilu
	}
	,{
		path: '/aboutus',
		name: '关于我们',
		component: aboutus
	}
	,{
		path: '/huodongbaoming',
		name: '活动报名',
		component: huodongbaoming
	}
	,{
		path: '/jifenlipin',
		name: '积分礼品',
		component: jifenlipin
	}
	,{
		path: '/discusszhiyuanhuodong',
		name: '志愿活动评论',
		component: discusszhiyuanhuodong
	}
	,{
		path: '/jifenduihuan',
		name: '积分兑换',
		component: jifenduihuan
	}
	,{
		path: '/storeup',
		name: '我的收藏',
		component: storeup
	}
	,{
		path: '/config',
		name: '轮播图管理',
		component: config
	}
	,{
		path: '/zhiyuanhuodong',
		name: '志愿活动',
		component: zhiyuanhuodong
	}
	,{
		path: '/zhiyuanzhe',
		name: '志愿者',
		component: zhiyuanzhe
	}
	,{
		path: '/newstype',
		name: '公告栏分类',
		component: newstype
	}
	]
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: {icon:'', title:'login'}
	},
	{
		path: '/register',
		name: 'register',
		component: register,
		meta: {icon:'', title:'register'}
	},
	{
		path: '*',
		component: NotFound
	}
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
	mode: 'hash',
	/*hash模式改为history*/
	routes // （缩写）相当于 routes: routes
})
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
export default router;
